.card{
    margin: 20px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 6px 18px -9px rgba(94, 93, 93, 0.75);
    transition: transform 0.1s ease-in;
    cursor: pointer;
    width: 356.5px;
    height: 466.5px;
}

.card:hover{
    transform: scale(1.07);
}

.card > img{
    object-fit: fill;
    min-width: 300px;
    min-height: 200px;
    width: 100%;
    
}

.card_info{
    margin-top: -9px;
    border-radius: 10px;
    padding: 20px;
    padding-top: 20px;
    border: 1;
}

.card_info > h2{
    font-size: 18px;
    font-weight: 600;
}

.card_info > h4{
    font-size: 14px;
    font-weight: 600;
}